.start-page-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
}

.triangle-left,
.triangle-right {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}

.triangle-left {
  border-width: 0 50vw 100vh 0;
  border-color: transparent green transparent transparent;
  left: -50vw;
  animation: slideInLeft 1.5s forwards;
}

.triangle-right {
  border-width: 100vh 50vw 0 0;
  border-color: blue transparent transparent transparent;
  right: -50vw;
  animation: slideInRight 1.5s forwards;
}

.center-text {
  position: relative;
  text-align: center;
  color: #000;
  opacity: 0;
  animation: fadeInText 2s forwards 1.5s; /* 삼각형 애니메이션 후 텍스트 나타나도록 딜레이 추가 */
}

.center-text h1 {
  font-size: 2em;
  margin: 0.5em;
}

.center-text p {
  font-size: 1.2em;
  margin-top: 1em;
}

/* 애니메이션 */
@keyframes slideInLeft {
  to {
    transform: translateX(50vw);
  }
}

@keyframes slideInRight {
  to {
    transform: translateX(-50vw);
  }
}

@keyframes fadeInText {
  to {
    opacity: 1;
  }
}
