.footer{
  display: flex;
  flex-direction: column;
	align-items: center;
	justify-content: center;
	height: inherit;
	// background-color: #333;
  .top{
    flex: 2;
    // background-color: #a33;
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: row;

    .left{
      flex: 3;
    }
    .center{
      flex: 1;
      
    }
    .right{
      flex: 3;
    }

    .parents{
      font-size: 1.2em;
      font-weight: bold;
    }
    .leading{
      font-size: 1.8em;
      font-weight: bold;
    }
  }

  .center{
    flex: 1;
    // background-color: #a66;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  .bottom{
    flex: 1;
    // background-color: #a99;
    width: 100%;
    align-items: center;
    justify-content: center;
  }


}