// myStyle.module.scss
$box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);

.bankAccount {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 1rem;

  .item1{
    flex: 4;
    display: flex;
    flex-direction: row;
    width: 100%;
    .left, .right {
      flex: 1;
      display: flex; // 추가
      justify-content: center; // 가로 가운데 정렬
      align-items: center; // 세로 가운데 정렬
      height: 100%;
      
    }
    
    .left {
      .card {
        background-color: rgb(160, 169, 245); // right의 색상
      }
    }

    .right {
      .card {
        background-color: rgb(241, 158, 241); // right의 색상
      }
    }
    
  }
  .item2{
    flex: 2;
    display: flex;
    justify-content: center; // 가로 가운데 정렬
    align-items: center; // 세로 가운데 정렬
    // background-color: #00a;
    width: 100%;
    
    .card{
      background-color: rgb(224, 238, 146); // right의 색상
      width: 80%;
    }
  }
  
}

.card {
  text-align: center; // 텍스트 가운데 정렬
  // background-color: #444;
  padding: 30px;
  border-radius: 12px;
  box-shadow: $box-shadow;
  height: 90%;
  width: 70%;
  margin: 10px;

  .accountItem {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    // background-color: beige;
    .title{
      flex: 1;
      font-size: 1.5em;
      font-weight: bold;
      .bank{
        font-size: 0.7em;
      }
    }

    .account{
      flex: 1;
      font-size: 1.5em;
      margin: 0.2em;
    }      

    .copyButton {
      flex: 1;
      padding: 5px 10px;
      background-color: #444;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
    }
  }


}
