/* 추가할 reset 조건 추가  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; //테두리 및 패딩을 포함 크기 계산 
  font-family: 'Poppins', sans-serif;
  overflow: hidden; /* 스크롤 없애기 */
}

html, body, .App {
  width: 100%;
  height: 100%;

  font-size: 14px;
  line-height: 22px;
}

// 모바일 환경 스타일
@media (max-width: 768px) {
  html, body, .App {
    font-size: 12px; // 모바일에서 폰트 크기 조정
    line-height: 20px; // 모바일에서 라인 높이 조정
  }
}