.main{
  display: flex;
  flex-direction: column;
	height: inherit;
	// background-color: #aaa;

  .title{
    flex: 1;
    // background-color: #393;

    width: 100%;

    .buttonGroup {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-family: 'Nanum Brush Script', cursive; // Or similar brush font
    
      .customButton {
        background: none;
        border: none;
        width: 100%;
        font-size: 1.2em;
        color: #aaa; // Darker color on hover
        cursor: pointer;
        outline: none;
        white-space: nowrap;
        &:hover {
          color: #9a9; // Darker color on hover
        }
        &:focus {
          outline: none;
          font-size: 1.6em; // Adjust size as needed
          color: #000; // Darker color on hover
          font-weight: bold;
        }
      }
    
      .separator {
        color: #000; // Gray color for separator
        padding-left: 10px;
        padding-right: 10px;
        font-size: 1.8em; // Match button size
      }
    }
    
  }
  .content{
    flex: 8;
    width: 100%;
  }
}