.Report {
  width: 100%;
  height: 100%;
  position: relative; // 오버레이 적용을 위해 상대 위치 설정
  border-radius: 10px;
  overflow: hidden; // 자식 요소의 넘침 방지
	color: #000; // 텍스트 색상

	.title{
    position: relative; // 배경 이미지보다 위에 표시
		z-index: 10; // 텍스트가 오버레이 위에 나타나도록 설정
    color: #000; // 텍스트 색상
		font-weight: bold;
    font-family: 'Nanum Brush Script', cursive; // 브러시 스타일 폰트 (필요 시 추가)
	}

	.textContent {
		position: absolute; // 절대 위치
    left: 50%; // 수평 중앙 정렬을 위해 설정
		bottom: 1px;
		transform: translateX(-50%); // 수평 중앙 정렬
		width: 100%;
		z-index: 10; // 텍스트가 오버레이 위에 나타나도록 설정
    color: #000; // 텍스트 색상
		font-weight: bold;
    font-family: 'Nanum Brush Script', cursive; // 브러시 스타일 폰트 (필요 시 추가)
    font-size: 1.3em; 
		line-height: 1.5em;
    padding: 20px;
    text-align: center;
  }

	.point{
		font-weight: bolder;
		background-color: #ef9;
		line-height: 1.8em;
		padding: 10px 15px;
		margin-top: 109px;
		border-radius: 10px;
		color: #000;
	}

	.human{
		font-weight: bolder;
		background-color: #494;
		line-height: 1.8em;
		padding: 5px 10px;
		border-radius: 10px;
		color: #fff;
	}

}
